.card {
    margin: 0.5rem;
    max-width: 100%;
    height: 45rem;
    /* border: 2px solid red; */
    background-image: linear-gradient(to right, rgba(0, 21, 90, 0.768), rgba(57, 242, 202, 0.695));
    /* box-shadow: 0 0 20px rgba(57, 242, 202, 0.695); */
    border-radius: 3rem;
    padding: 0.2rem;
    /* overflow: hidden; */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.card img {
    width: 100%;
    height: 40%;
    border-radius: 3rem;
    box-shadow: 0 0 20px rgba(57, 242, 202, 0.695);
}

#nameRat,
.rowDetails,
.pricing {
    margin: 0.3rem 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

#nameRat h1 {
    font-size: 1.6rem;
    color: rgb(248, 244, 5);
    text-shadow: 0 0 5px black;

}

.stars {
    font-size: 1.7rem;
    display: flex;
    justify-content: right;
    width: 10rem;
}

.pricing p {
    font-size: 2rem;
    color: white;
    text-shadow: 0 0 5px black;
    text-align: right;
}

.rowDetails h1,
.pricing h1 {
    width: 50%;
    font-size: 1.4rem;
    color: white;
    text-shadow: 0 0 5px black;
    text-align: left;
}

.rowDetails p {
    font-size: 1.2rem;
    color: white;
    text-shadow: 0 0 5px black;
    text-align: right;
}

.btn {
    width: 30%;
    height: 2rem;
    overflow: hidden;
    border-radius:0 10px 0 10px;
    background-image: linear-gradient(to right, rgba(0, 21, 90, 0.768), rgba(57, 242, 202, 0.695));
}

.btn button {
    width: 100%;
    height: 100%;
    background: transparent;
    color: white;
    font-size: 1rem;
    border-radius:0 10px 0 10px;
    cursor: pointer;
}


/* mobile animation part */
.mobile{
    margin: 0 0.5rem;
    color: white;
    text-shadow: 0 0 5px black;
    font-size: 1.2rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.trin-trin
{
    animation-name: trin;
    animation-duration: 1.5s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

.icon-phone
{
    background-image: url('data:image/png;base64,');	
    background-repeat: no-repeat;
    background-size: 20px;
    height: 20px;
    width: 20px;
    margin: 0 1rem;

}
.icon-email
{
    background-image: url('https://w7.pngwing.com/pngs/595/338/png-transparent-icon-email-email-miscellaneous-angle-text-thumbnail.png');	
    background-repeat: no-repeat;
    background-size: 20px;
    height: 20px;
    width: 20px;
    margin: 0 1rem;

}
.variety{
    max-width: 100%;
    display: grid;
    grid-template-columns: auto auto auto auto;
    justify-content: center;
    align-items: center;
}
.small_card{
    margin: 0.2rem;
    padding: 0.4rem;
    background-color: #ffffff69;
    color: black;
    font-weight: bold;
    text-align: center;
    border-radius: 1rem;
   
}

@keyframes trin {

    from {
        transform:rotate3d(0, 0, 1, 0deg);
    }
    20%, 32%, 44%, 56%, 68% {
        transform: rotate3d(0, 0, 1, 0deg);
    }
    23%, 35%, 47%, 59%, 71% {
        transform: rotate3d(0,0,1,15deg);
    }
    26%, 38%, 50%, 62%, 74% {
        transform: rotate3d(0,0,1,0deg);
    }
    29%, 41%, 53%, 65%, 77% {
        transform: rotate3d(0,0,1,-15deg);
    }
    80% {
        transform:rotate3d(0, 0, 1, 0deg);
    }

}
